.containerArea{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 150px;
    width: 100%;
    min-height: 100vh;
    background-color: rgb(252, 252, 252);
    background-image: url('../../chatbot_area/chatbotArea/image/backChatBot.png');
    background-position: center;
    background-size: cover;
}

.containerAdding{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 20%;
    height: 80%;
    border-radius: 10px;
}

@media screen and (max-width: 1200px){
    .containerArea{
        padding-top: 80px;
    }
}